@import "variables";

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 505px;
  margin: 0;
  padding: 0;

  background-image: url("../img/index-background-mobile@1x.jpg"), linear-gradient(to top, #fff 39.4%, #65a93c 39.4%);
  background-repeat: no-repeat;
  background-position: top;
  background-size: 320px 306px, auto;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/index-background-mobile@2x.jpg"), linear-gradient(to top, #fff 39.4%, #65a93c 39.4%);
    background-repeat: no-repeat;
    background-position: top;
    background-size: 320px 306px, auto; } }

.page__title {
  width: 90%;
  margin: 20px;
  margin-top: 29px;
  margin-bottom: 25px;
  padding: 0;

  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  font-family: Oswald, Arial, sans-serif;
  color: #ffffff;

  text-align: center; }

.page__slogan {
  margin: 0;
  margin-bottom: 28px;
  padding: 0;

  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  font-family: Oswald, Arial, sans-serif;
  text-transform: uppercase;
  color: #ffffff; }

.page__link-select {
  width: 87.5%;
  padding-top: 10px;
  padding-bottom: 11px;

  text-align: center; }

@media (min-width: $tablet-width) {
  .page {
    align-items: start;
    min-height: 843px;
    margin-left: 15.2%;
    background-color: #ffffff;
    background-image: none; }

  .page__title {
    width: 53.9%;
    margin: 33px 20px 41px 0;

    font-size: 60px;
    line-height: 60px;

    text-align: start;
    color: #111111; }

  .page__slogan {
    margin-bottom: 49px;

    font-size: 20px;
    line-height: 20px;

    color: #111111; }

  .page__image {
    position: absolute;
    top: 342px;
    right: 4%;

    width: 92%;
    max-width: 720px;
    height: auto; }

  .page__link-select {
    width: 31.8%;
    padding-top: 14px;
    padding-bottom: 16px;

    z-index: 2; } }

@media (min-width: $desktop-width) {
  .page {
    min-height: 526px;
    margin-bottom: 80px;
    margin-left: 13.2%;
    padding-top: 170px;

    background-image: url("../img/index-background-desktop@1x.jpg"), linear-gradient(to left, #65a93c 50%, #fff 50%);
    background-repeat: no-repeat;
    background-position: 73.5% 0;
    background-size: 720px 696px, auto; }

  .page__title {
    width: 29%;
    margin-top: 52px;
    margin-bottom: 41px; }

  .page__slogan {
    margin-bottom: 53px; }

  .page__image {
    top: 144px;
    right: 22.9%;

    width: 38.2%;

    z-index: 1; }

  .page__link-select {
    width: 17%;
    padding-top: 15px;
    padding-bottom: 17px; }

  .page--backround-desktop {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
    display: block;
    width: 50%;
    min-height: 696px;
    margin: 0;
    padding: 0;

    background-color: #63a73a;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-position: top;
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/index-background-desktop@2x.jpg");
      background-size: 50% auto; } } }
