@import "variables";

.container-footer {
  margin: 0;
  margin-top: -6px;
  padding: 0;

  background-color: #f2f2f2; }

.container-footer--flex {
  display: flex;
  flex-direction: column; }

.container-footer__logo {
  position: relative;
  margin: 0;
  padding: 0;

  text-align: center; }

.container-footer__logo-icon {
  padding-top: 40px;
  padding-bottom: 30px; }

.container-footer__logo:hover .container-footer__logo-icon {
  opacity: 0.7; }

.container-footer__logo:active .container-footer__logo-icon {
  opacity: 0.3; }

.container-footer__logo::after {
  content: " ";
  position: absolute;
  top: 94px;
  left: 1px;
  width: 100%;
  height: 1px;
  background-color: #d9d9d9; }

.container-footer__link {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  margin-top: 10px;
  margin-bottom: 16px;
  padding: 0;

  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  font-family: Arial, sans-serif;
  color: #444444;

  text-decoration: none; }

.container-footer__link::before {
  content: " ";
  position: absolute;
  top: -15px;
  left: 1px;
  width: 100%;
  height: 1px;
  background-color: #d9d9d9; }

.container-footer__link-icon {
  margin-top: 1px;
  margin-right: -7px; }

.container-footer__link-icon {
  fill: #666666; }

.container-footer__link:hover .container-footer__link-icon {
  fill: $bg-btn-green; }

.container-footer__link:active .container-footer__link-icon {
  fill-opacity: 0.3; }

@media (min-width: $tablet-width) {
  .container-footer--flex {
    flex-direction: row;
    justify-content: space-between; }

  .container-footer__logo-icon {
    padding-top: 63px;
    padding-bottom: 58px; }

  .container-footer__link {
    margin-top: -4px;
    margin-bottom: 0; }

  .container-footer__link-icon {
    margin-top: 3px;
    margin-left: 17px; }

  .container-footer__logo::after,
  .container-footer__link::before {
    display: none; } }

@media (min-width: $desktop-width) {
  .container-footer--flex {
    padding-top: 12px; }

  .container-footer__logo-icon {
    padding-top: 58px;
    padding-bottom: 60px; }

  .container-footer__link {
    margin-top: -6px; }

  .container-footer__link-icon {
    margin-top: 2px; } }
