@import "variables";

.article {
  margin: 0;
  padding: 0;

  background-color: #f2f2f2; }

.article--flex {
  display: flex;
  flex-direction: column; }

.article__title {
  margin: 0;
  margin-top: 19px;
  margin-bottom: 23px;
  padding: 0;

  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
  font-family: Oswald, Arial, sans-serif;
  color: #111111; }

.article__text {
  width: 95.3%;
  margin: 0;
  margin-bottom: 25px;
  padding: 0;

  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  font-family: Arial, sans-serif;
  color: #444444; }

.article__special {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0; }

.box-cpecial {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 43.7%;
  margin: 0;
  margin-bottom: 34px;
  padding: 0;

  border: 1px solid #cdcdcd;
  border-radius: 2px; }

.box-cpecial__value {
  margin: 0;
  margin-top: 13px;
  margin-bottom: 17px;
  padding: 0;

  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  font-family: Oswald, Arial, sans-serif;
  text-transform: uppercase;
  color: #111111; }

.box-cpecial__field {
  position: absolute;
  top: 46px;

  display: inline-block;
  width: 45.6%;
  margin: 0;
  padding: 0;
  padding-left: 16px;
  padding-right: 16px;

  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  font-family: Arial, sans-serif;
  color: #444444;

  text-align: center;
  vertical-align: baseline;
  background: #f2f2f2; }

.article__info {
  margin: 0;
  margin-bottom: 34px;
  padding: 0;

  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  font-family: Oswald, Arial, sans-serif;
  text-transform: uppercase;
  color: #111111;

  text-align: center; }

.article__info span {
  margin-left: 3px; }

@media (min-width: $tablet-width) {
  .article {
    background-color: #ffffff; }

  .article__title {
    margin-top: -6px;
    margin-bottom: 63px;

    font-size: 60px; }

  .article__text {
    width: 95.4%;
    margin-bottom: 53px;

    font-size: 16px;
    line-height: 24px; }

  .article__box--flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }

  .article__special {
    justify-content: space-between;
    width: 51.6%;
    margin-bottom: 51px; }

  .box-cpecial__value {
    margin-top: 14px;
    margin-bottom: 19px;

    font-size: 30px;
    line-height: 37px; }

  .box-cpecial__field {
    top: 62px;

    width: 60.7%;
    padding-left: 8px;
    padding-right: 10px;

    font-size: 14px;
    line-height: 14px;

    background: #ffffff; }

  .box-cpecial__field--time {
    width: 79%; }

  .article__info {
    width: 24.72%;
    margin-right: 1px;
    margin-bottom: 0;
    padding-top: 7px;

    font-size: 20px;
    line-height: 29px;

    text-align: right; }

  .article__info span {
    display: inline-block;
    vertical-align: baseline; } }

@media (min-width: $desktop-width) {
  .article {
    width: 45%;
    margin-bottom: 70px;

    background: #f2f2f2; }

  .article--flex {
    position: relative;
    margin: 0;
    margin-left: 17%;
    margin-right: 16%; }

  .article__title {
    position: absolute;
    top: -129px;

    background: #ffffff; }

  .article__text {
    width: 100%;
    margin-top: 71px;
    margin-bottom: 68px; }

  .article__box--flex {
    flex-direction: column; }

  .article__special {
    width: 83.8%;
    margin-bottom: 50px; }

  .box-cpecial {
    margin-bottom: 0; }

  .box-cpecial__value {
    margin-top: 14px;
    margin-bottom: 19px;

    font-size: 30px;
    line-height: 37px; }

  .box-cpecial__field {
    padding-right: 8px;
    width: 62.7%;
    background: #f2f2f2; }

  .box-cpecial__field--time {
    width: 80%; }

  .article__info {
    width: 100%;

    text-align: left; }

  .article__info span {
    margin-left: 64px; } }
